import {CoreColors, CoreFonts} from "core/assets/fonts"
import {ImagesMetaData} from "core/types"

export module Sounds {
  export const keys: string[] = []
}

export module Translations {
  export const remember = "remember"
  export const tutor1 = "tutor1"
  export const tutor2 = "tutor2"
  export const tutor3 = "tutor3"
}

export module Fonts {

  export const purple = 0x7A46D3
  export const highlight = 0xECFDAC

  export const word = {
    fontSize: 45, font: CoreFonts.MontserratSemiBold, fill: CoreColors.black, align: "center"
  }
  export const button = {
    fontSize: 55, font: CoreFonts.MontserratSemiBold, fill: CoreColors.white, align: "center"
  }
}

export module Images {

  export const bg = "bg"
  export const empty = "empty"
  export const btn_white = "btn_white"
  export const btn_hover = "btn_hover"
  export const btn_hightlight = "btn_hightlight"
  export const btn_outline = "btn_outline"
  export const icon = (number: number, index: number) => `icon_${number}_${index}`
  export const number = (number: number) => `number_${number}`
  export const data: ImagesMetaData = {
    sprites: []
  }

}




