import {createLevelsFromArray} from "core/types/logic"

export interface Level {
  numberCount: number
  hideIconCount: number
  iconCount: number
}

function level(
  numberCount: number,
  showIconCount: number,
  iconCount: number
): Level {
  return {numberCount, hideIconCount: numberCount - showIconCount, iconCount}
}

export const levels = createLevelsFromArray([
  level(3, 3, 5),
  level(4, 3, 5),
  level(5, 2, 5),
  level(6, 2, 5),
  level(7, 2, 4),
  level(8, 2, 4),
  level(9, 1, 4),
  level(10, 1, 3),
  level(11, 1, 3),
  level(12, 0, 3),
  level(13, 0, 2),
  level(14, 0, 2),
  level(15, 0, 2),
  level(16, 0, 1),
  level(17, 0, 1),
  level(18, 0, 1),
])



