import {Tweens} from "core/ui/animations/tweens"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/index"
import {OnInit} from "core/ui/components/onInit"
import {Img} from "core/ui/controls/img"
import {Images} from "games/mnemonics/wordassociation/assets"
import * as _ from 'lodash'

const durations = {
  move: 800,

  delay: 0,
  delayBird: 200
}

export class IntroComponent extends GameComponent implements OnInit {
  positions = Sizes.create({
    bird3: {x: 728}
  })

  bg = this.addImage(Images.bg)
  bg1 = this.addImage("intro_bg_1")
  bg2 = this.addImage("intro_bg_2").updatePos(this.bg1.width, 0)
  bg3 = this.addImage("intro_bg_3").updatePos(this.bg1.width, this.bg2.height)
  bg5 = this.addImage("intro_bg_5").updatePos(0, this.bg2.height)
  bg4 = this.addImage("intro_bg_4").updatePos(0, this.bg1.height + this.bg5.height)
  bird2 = this.addImage("intro_bird_2").updatePos(this.bg1.width, this.bg1.height).anchorOne()
  bird1 = this.addImage("intro_bird_1").updatePos(this.bg1.width, this.bg1.height).anchorOneY()
  bird3 = this.addImage("intro_bird_3").updatePos(this.positions.bird3.x, this.bg1.height)

  moveTo(target: Img, to: any, delay = false) {
    return Tweens.start({
      target,
      to,
      notStart: true,
      delay: durations.delay + (delay ? durations.delayBird : 0),
      duration: durations.move
    })

  }

  tweens: Phaser.Tween[] = []

  createTween() {
    this.tweens = [
      this.moveTo(this.bg1, {x: -this.bg1.width, y: -this.bg1.height}),
      this.moveTo(this.bg2, {x: Sizes.width + this.bg2.width, y: -this.bg2.height}),
      this.moveTo(this.bg3, {x: Sizes.width + this.bg3.width, y: Sizes.height + this.bg3.height}),
      this.moveTo(this.bg4, {x: -this.bg4.width, y: Sizes.height + this.bg4.height}),
      this.moveTo(this.bg5, {x: -this.bg5.width}),

      this.moveTo(this.bird2, {x: -this.bg1.width, y: -this.bg1.height}, true),
      this.moveTo(this.bird1, {x: Sizes.width + this.bg2.width, y: -this.bg2.height}, true),
      this.moveTo(this.bird3, {x: -this.bg4.width, y: Sizes.height + this.bg4.height}, true)
    ]
    return _.last(this.tweens)!

  }

  start() {
    this.tweens.forEach(x => x.start())
  }

  onInit(): void {
  }

}




