import {CoreColorNumbers} from "core/assets/fonts"
import {AnswerStatus, ILastAnswer} from "core/types/logic"
import {BiValue} from "core/ui/common/positions"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/games/gameComponent"
import {TutorialTextComponent} from "core/ui/components/games/gameUI/tutorialTextComponent"
import {OnInit} from "core/ui/components/onInit"
import {HightlightBehavior} from "core/ui/controls/behaviors/highlightBehavior"
import {Btn} from "core/ui/controls/btn"
import {Control} from "core/ui/controls/control"
import {ControlUtl} from "core/ui/controls/controlUtl"
import {BtnDefaultImg} from "core/ui/controls/GraphicsImg"
import {Img} from "core/ui/controls/img"
import {Fonts, Images, Translations} from "games/mnemonics/numberassociation/assets"
import * as _ from 'lodash'
import {CoreSounds} from "../../../../core/assets/sounds"
import {Task} from "../logic"

const biValues = BiValue.create({
  scale: 1
}, {
  scale: 0.84
})

export class QuestionComponent extends GameComponent implements OnInit {

  positions = Sizes.create({
    btnSize: {width: 500, height: 100},
    clickArea: {width: 100, height: 100},
    clickAreaIcon: {width: 170, height: 170},
    shift: -70
  })

  biPositions = Sizes.bi({
    marginIcon: {x: -50},
    marginAnswers: {x: -50, y: -100},
    numbers: {x: Sizes.widthNormal / 2, y: 550},
    numbersTutorial: {x: Sizes.widthNormal / 2, y: 580},
    icons: {x: Sizes.widthNormal / 2, y: 1030},
    marginNumbers: {x: -40}
  }, {
    marginIcon: {x: -80},
    marginAnswers: {x: -110, y: -80},
    numbers: {x: Sizes.widthNormal / 2, y: 530},
    numbersTutorial: {x: Sizes.widthNormal / 2, y: 580},
    icons: {x: Sizes.widthNormal / 2, y: 1120},
    marginNumbers: {x: -70}
  })

  numbers: Control[] = []
  icons: Btn[] = []
  index = 0

  numbersGroup = this.add(new Control()).anchorCenter()
    .setBiPos(this.isTutorial ? this.biPositions.numbersTutorial : this.biPositions.numbers)

  iconsGroup = this.add(new Control()).anchorCenter().setBiPos(this.biPositions.icons)

  rememberBtn = this.add(new Btn(this.translate(Translations.remember),
    () => this.onRemember(), undefined, new BtnDefaultImg({
      color: Fonts.purple,
      size: this.positions.btnSize
    }))).anchorCenter().setBiPos(this.biPositions.icons)

  answersGroup = this.add(new Control()).anchorCenter().setBiPos(this.biPositions.icons)

  tutorial: TutorialTextComponent

  onRemember() {
    this.playSound(CoreSounds.click, false, true)
    if (this.isTutorial) {
      this.tutorial.setProps(this.translate(Translations.tutor3))
    }
    const task = this.getCurrentTask<Task>()
    task.hideIconIndexes.forEach(i => {
      this.numbers[i].childAt<Img>(1).key = this.gameKey(Images.empty)
    })
    this.rememberBtn.visible = false
    this.answersGroup.visible = true
    this.numbers[0].getBehavior(HightlightBehavior).enableHighlight(true)
    this.updateNumbersPositions()
    this.answersAreReady()
  }

  chooseIconsComplete() {
    if (this.isTutorial) {
      this.tutorial.setProps(this.translate(Translations.tutor2))
    }
    this.enableHighlight()
    this.index = 0
    this.rememberBtn.visible = true
    this.iconsGroup.visible = false
  }

  onChooseIcon(number: number, imageIndex: number) {
    this.playSound(CoreSounds.click, false, true)
    this.numbers[this.index].childAt<Img>(1).key = this.gameKey(Images.icon(number, imageIndex))
    this.numbers[this.index].childAt<Img>(0).key = this.gameKey(Images.btn_white)
    this.index += 1
    if (this.index >= this.numbers.length) {
      this.chooseIconsComplete()
    } else {
      this.createCurrentNumberIcons()
    }
    this.updateNumbersPositions()
  }

  onAnswer(answer: ILastAnswer): void {
    if (answer.status != AnswerStatus.Error) {

      const index = answer.correctAnswerIndex!
      const number = this.numbers[index]
      number.childAt<Img>(0).key = this.gameKey(Images.btn_outline)
      number.childAt<Img>(1).key = this.gameKey(Images.number(number.tag))
      this.enableHighlight(index + 1)
    }
  }

  enableHighlight(index = this.index) {
    this.numbers.forEach((x, i) => {
      const highlight = i == (index)
      const tint = highlight && x.childAt<Img>(0).key == this.gameKey(Images.btn_outline)
        ? Fonts.highlight
        : CoreColorNumbers.white
      x.childAt<Img>(0).img.tint = tint
      x.childAt<Img>(1).img.tint = tint
      return x.getBehavior(HightlightBehavior).enableHighlight(highlight)
    })
  }

  createCurrentNumberIcons() {
    const task = this.getCurrentTask<Task>()
    const icons = task.icons[this.index]
    const number = this.numbers[this.index].tag as number
    this.enableHighlight()
    this.iconsGroup.removeAll(true)
    this.icons = icons.map(i => {
      const hover = this.makeImage(Images.btn_hover).fluentUpdate(x => x.visible = false)
      const back = ControlUtl.stack(
        hover,
        this.makeImage(Images.btn_white),
        this.makeImage(Images.icon(number, i)))
      const icon = new Btn("", () => this.onChooseIcon(number, i), undefined, back,
        {clickArea: this.positions.clickAreaIcon})
      icon.onHover(out => hover.visible = !out)
      return this.iconsGroup.add(icon)
    })

    this.updateIconsPositions()
  }

  updateIconsPositions() {
    const colCount = this.isPortrait ? 5 : 5
    Sizes.tableFromList(this.icons, colCount, this.current(this.biPositions.marginIcon))
    this.iconsGroup.updatePivot()
  }

  updateNumbersPositions() {
    const colCount = this.isPortrait ? 4 : 6
    Sizes.tableFromList(this.numbers, colCount, this.current(this.biPositions.marginNumbers))
    this.numbersGroup.updatePivot()
  }

  onNewTask(task: Task): void {
    if (this.isTutorial) {
      this.tutorial.setProps(this.translate(Translations.tutor1))
    }
    this.index = 0

    this.rememberBtn.visible = false
    this.answersGroup.visible = false
    this.iconsGroup.visible = true

    this.numbersGroup.removeAll(true)
    this.numbers = task.numbers.map(number => {
      const img = this.makeImage(Images.number(number))

      return this.numbersGroup.add(ControlUtl.stack(this.makeImage(Images.btn_outline), img))
        .addBehavior(new HightlightBehavior()).fluentUpdate(x => {
          x.tag = number
        })
    })
    this.updateNumbersPositions()

    this.createCurrentNumberIcons()
  }

  onInit(): void {
    const btns = [..._.range(1, 10), 0].map(x => {
      const hover = this.makeImage(Images.btn_hover).fluentUpdate(x => x.visible = false)
      const number = this.makeImage(Images.number(x))
      const back = ControlUtl.stack(hover, number)
      const btn = this.answersGroup.add(new Btn("", () => {
        }, undefined, back,
        {clickArea: this.positions.clickArea}))
      btn.onHover(out => {
        hover.visible = !out
      })
      this.addAnswerBehavior(btn, x, {dark: true, location: "top", margin: this.positions.shift})
      return btn
    })

    this.onBiValueChange(biValues.scale, (c, v) => {
      this.iconsGroup.symmetricScale = v
      Sizes.tableFromList(btns, 5, this.current(this.biPositions.marginAnswers))
      this.answersGroup.updatePivot()
      this.updateNumbersPositions()
      this.updateIconsPositions()
    })

    if (this.isTutorial) {
      this.tutorial = this.add(this.createComponent(TutorialTextComponent).make({textAlign: "center"}))
    }

    this.rememberBtn.visible = false
    this.answersGroup.visible = false
  }

}



