import {GameLogic} from "core/logic"
import {RandomGenerator} from "core/utils"
import {CircleSeq, UniqSeq} from "core/utils/circleSeq"
import * as _ from 'lodash'
import {Level} from "./levels"

const maxIconCount = 5

export type Task = {
  numbers: number[]
  hideIconIndexes: number[]
  icons: number[][]
  answers: { seq: number[] }
}

const allNumbers = _.range(10)

export class Logic extends GameLogic<Task, Level> {
  rng = new RandomGenerator()
  numberSeq = new UniqSeq(0)
  iconSeqs = allNumbers.map(() => new CircleSeq(this.rng.range(maxIconCount)))

  nextTask(level: Level) {
    this.numberSeq.reset()
    const numbers = this.numberSeq.nextSubsetFrom(allNumbers, level.numberCount)
    const icons = numbers.map(number => this.iconSeqs[number].nextArray(level.iconCount))
    const answers = {seq: numbers}
    const hideIconIndexes = this.rng.subset(_.range(numbers.length), level.hideIconCount)

    const task: Task = {
      numbers,
      icons,
      hideIconIndexes,
      answers
    }

    return task
  }

}





